/* bannar animation starts */
.ban_h1 {
  display: inline-block;
  font-family: Georgia, "Times New Roman", Times, serif;
  letter-spacing: 4px;
  animation-name: bannarAnimation;
  animation-duration: 4s;
  opacity: 0;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
}
@keyframes bannarAnimation {
  0% {
    opacity: 0;
    transform: translateX(-100px) skewY(1deg) skewX(10deg);
    filter: blur(10px);
  }
  25% {
    opacity: 1;
    transform: translateX(0px) skewY(0deg) skewX(0deg);
    filter: blur(0px);
  }
  75% {
    opacity: 1;
    transform: translateX(0px) skewY(0deg) skewX(0deg);
    filter: blur(0px);
  }
  100% {
    opacity: 0;
    transform: translateX(-100px) skewY(2deg) skewX(10deg);

    filter: blur(10px);
  }
}
/* bannar animation ends */
.bannar_bottom {
  position: absolute;
