.f_ul li a {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
}
.f_ul li .drop_down {
  position: absolute;
  top: 100px;
  left: 35px;
  background: rgb(242 242 249);
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  width: 152px;
  padding: 2px;
  text-align: left;
  opacity: 0;
  visibility: hidden;
  transition: 0.4s;
  transition-delay: 0.1s;
}

.f_ul li:hover .drop_down {
  opacity: 1;
  visibility: visible;
  top: 33.5px;
}
.f_ul li .drop_down li {
  display: block;
  position: relative;
}
.f_ul li .drop_down li a {
  display: block;
  position: relative;
  padding: 10px 10px;
  transition: 0.4s;
  font-size: 15px;
  font-weight: 500;
  border-bottom: 1px solid #e3e3e3;
}

.f_ul li .drop_down li a:hover {
  color: #328fce;
}
.f_ul a {
  text-decoration: none;
  padding: 0 50px;
  color: rgb(29, 28, 28);
}
